import * as React from 'react';
import Grid from '@mui/material/Grid';
import Link from '@mui/material/Link';
import Typography from '@mui/material/Typography';
import Logo from '../../images/logo.svg';
import MailIcon from '../../images/icon/contact_mail.svg';
import LineIcon from '../../images/icon/contact_line.svg';
import TwitterIcon from '../../images/icon/contact_twitter.svg';
import InstagramIcon from '../../images/icon/contact_instagram.svg';
import { scrollToSection } from '../../utils/scroll';
import Section from '../section';
import SectionTitle from '../section-title';

const mail_address = 'addictgymjp@gmail.com';

const List = ({ titles }) => (
  <ul
    style={{
      margin: 0,
      padding: 0,
      listStyleType: 'none'
    }}
  >
    {titles.map((title, i) => {
      const lowerTitle = title.toLowerCase();

      return (
        <li
          key={i}
          style={{
            marginBottom: '1em'
          }}
        >
          <Link
            href={`#${lowerTitle}`}
            underline='hover'
            color='#fff'
            onClick={e => {
              e.preventDefault();
              scrollToSection(lowerTitle);
            }}
          >
            <Typography
              sx={{
                textAlign: {
                  sm: 'left',
                  xs: 'center'
                }
              }}
            >
              {title}
            </Typography>
          </Link>
        </li>
      );
    })}
  </ul>
);

const SectionContact = () => (
  <Section id='contact' theme='dark'>
    <SectionTitle theme='dark'>Contact</SectionTitle>

    <Typography
      align='center'
      sx={{
        my: 4
      }}
    >
      <img
        src={MailIcon}
        alt={mail_address}
        width={24}
        height={18}
        style={{
          marginRight: '0.5em',
          pointerEvents: 'none',
          verticalAlign: 'text-bottom'
        }}
      />
      <Link href={`mailto:${mail_address}`} color='#fff'>
        {mail_address}
      </Link>
    </Typography>

    <Grid container>
      <Grid
        item
        xs={10}
        sm={6}
        md={4}
        sx={{
          margin: '0 auto'
        }}
      >
        <Grid container>
          {[
            {
              name: 'LINE',
              icon: LineIcon,
              link: 'https://works.do/R/ti/p/addictgymjp@addictgym'
            },
            {
              name: 'Twitter',
              icon: TwitterIcon,
              link: 'https://twitter.com/addictgymjp/'
            },
            {
              name: 'Instagram',
              icon: InstagramIcon,
              link: 'https://www.instagram.com/addictgymjp/'
            }
          ].map(({ name, icon, link }) => (
            <Link
              key={name}
              href={link}
              target='_blank'
              rel='noopener noreferrer'
              sx={{
                display: 'block',
                margin: '0 auto'
              }}
            >
              <img src={icon} alt={name} width={44} height={44} />
            </Link>
          ))}
        </Grid>
      </Grid>
    </Grid>

    <Grid
      container
      sx={{
        mt: 10
      }}
    >
      <Grid item xs={12} md={3}>
        <img
          src={Logo}
          alt='addict gym'
          width={120}
          height={85}
          style={{
            display: 'block',
            margin: '0 auto 3em'
          }}
        />
      </Grid>
      <Grid item xs={6} md={2}>
        <List titles={['About', 'Facility', 'Pricing', 'Booking']} />
      </Grid>
      <Grid item xs={6} md={2}>
        <List titles={['Trainer', 'Access', 'Contact']} />
      </Grid>
    </Grid>

    <Grid container>
      <Typography
        variant='body2'
        sx={{
          width: '100%',
          mt: 3,
          textAlign: {
            sm: 'right',
            xs: 'center'
          }
        }}
      >
        &copy; 2023 addict gym
      </Typography>
    </Grid>
  </Section>
);

export default SectionContact;
